// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

$color-neon-blue:                   #2c39f2;
$color-torch-red:                   #ff1553;
$color-lochmara:                    #3d70ab;
$color-alice-blue:                  #f9fbfd;
$color-tangaroa:                    #011c40;
$color-tangaroa-70:                 rgba($color-tangaroa, 0.7);
$color-tangaroa-0:                  rgba($color-tangaroa, 0);
$color-default-black-25:            rgba($color-default-black, 0.25);
$color-pattens-blue:                #d8e5f4;
$color-quartz:                      #d8d8d9;
$color-pale-cornflower-blue:        #abd4fa;
$prussian-blue:                     #002b66;
$prussian-blue-50:                  rgba($prussian-blue, 0.5);
$romance:                           #f1efe9;

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

//    html   
$fz-default:                        16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-placeholder:                    "Placeholder", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

//      $tf-default,     
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-520:                            520px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
